<template>
    <div class="pcen-layout">
        <header class="clearfix pcen-jifen-bar">
            <a onclick="window.history.go(-1);" class="jifen-back-btn"></a>
            <h2 class="jifen-title">当前积分</h2>
            <span class="jifen-detail-text" @click="goJiFenDetailPage()">详细</span>
        </header>
        <section class="jifen-item1" v-show="!isShow">
            <div class="jifen-alltotal-box">
                <span class="jifen-alltotal">{{jiFenObj.current_credit}}</span>
            </div>
        </section>
        <section class="jifen-item2" v-show="!isShow">
            <ul>
                <li class="jifen-sm-item">
                    <p class="jifen-get-t">
                        积分获得
                    </p>
                    <span class="jifen-get-num">
                        {{jiFenObj.credit_increased_sum}}
                    </span>
                </li>
                <li class="jifen-sm-item">
                    <p class="jifen-get-t">
                        积分使用
                    </p>
                    <span class="jifen-get-num jifen-use-num">
                        {{jiFenObj.credit_used_sum}}
                    </span>
                </li>
            </ul>
        </section>
        <section class="jifen-item3">
            <a @click="goGetJiFenPage($event)" class="how-get-jifen">如何获得积分</a>
        </section>
      <NoData v-show="isShow"></NoData>
    </div>
</template>
<script>
  import Api from '../../api/api';
  import {getJiFenInfo} from '../../api/jifen';
  import NoData from '../nodata/NoData.vue';
  import logUtil from '../../utils/log';
    export default{
      components:{
        NoData
      },
      data(){
        return{
            jiFenObj:{},
            jiFenTotal:0,
            isShow:false,
        }
      },
      mounted(){
        this.$nextTick(function () {
          this.getUserJIfen();
        });
      },
        methods:{
            /**
             * go personal jiFen page
             */
            goJiFenDetailPage(){
                this.$router.push({path: '/jifendetail', replace: true});
            },
            getUserJIfen(){
                 const _this = this;
                 getJiFenInfo(`${Api.MYTIEZI}${localStorage.getItem('userId')}/credits/`)
                   .then((res)=>{
                     _this.initJiFenData(res);
                   },(err)=>{
                     _this.dealErr();
                   })
            },
            initJiFenData(res){
                  logUtil.printLog("获取到的用户积分是:",res);
                  if(res.status ===200 && res.data!==null){
                      this.jiFenObj = res.data.data.detail;
                      this.jiFenTotal = this.jiFenObj.current_credit+this.jiFenObj.credit_used_sum;
                      this.isShow = false;
                  }else{
                        this.isShow = true;
                  }
            },
            dealErr(){
                this.isShow = true;
                this.$store.dispatch('hideLoading');
                this.showMsg('获取积分信息失败,请重试!');
            },
            showMsg(str){
              this.$toast({
                message: str,
                duration: 2000,
              });
            },
          /**
           * 跳转到获取积分的界面
           * @param $event
           */
            goGetJiFenPage($event){
              this.$router.push({path: '/gjifen', replace: true});
              $event.preventDefault();
            },
        },

    }
</script>
<style scoped>
    @import "../../assets/css/pcenjifen.css";
    body {
        background-color: #ffffff;
    }
</style>



