import axios from 'axios';
/**
 * 获取用户积分规则,或
 * 获取用户积分信息
 * @param url
 * @returns {*}
 */
export function getJiFenInfo(url) {
  return axios.get(url).then((res)=>{
    return Promise.resolve(res);
  },(err)=>{
    return Promise.reject(err);
  })
}





